body,
html {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    background-color: grey;
}

#root {
    position: absolute;
}

.main-layout {
    position: relative;
    top: 31px;
}

.main-content {
    position: relative;
    display: flex;
    align-items: center;

    margin: 0px 25px;
}

.mw-header {
    position: fixed;
    height: 31px;
    top: 0px;
    left: 0px;
    right: 0px;
    z-index: 100;

    display: flex;
    flex-direction: row;
    justify-content: stretch;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
    background-color: orange;
}

.mw-header .nav {
    flex: 1;
}
.mw-header .title {
    align-items: center;
    flex: 1;
}

.title {
    display: flex;
    color: aliceblue;
}

.nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.nav > * {
    margin: 5px 10px;
}

.media-grid {
    display: flex;

    flex-wrap: wrap;

    justify-items: stretch;
}

.media-grid .media-item {
    height: 300px;
    width: 200px;
    color: white;
    box-shadow: 6px 6px 0px 0px #222222;
    background-color: black;
    margin: 5px;
    overflow: hidden;
    text-align: center;
}

.media-item {
    position: relative;
    padding: 10px;
}

.media-item-text {
    position: relative;
    z-index: 1;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 85%;
    background-color: #222222;
}

.media-item-cover {
    position: absolute;
    top: 0px;
    height: 100%;
    bottom: 0px;
    width: 100%;
    left: 0px;
    object-fit: cover;
}
